.footer{
    background: #CFD2CF;
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
    margin-top: 7rem;

}
/* var(--color-primary); */

.footer a{
    color: var(--color-bg);
    text-decoration: none;
}

.footer-logo{
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
}

.permalinks {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer-socials{
    display: flex;
    justify-content: center;
     gap: 1rem;
     margin-bottom: 4rem;
}

.footer-socials a {
    background: var(--color-bg);
    color: #CFD2CF;
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;

}


.footer-socials a:hover {
    background: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
}

.footer-copyright {
    margin-bottom: 4rem;
    color: var(--color-bg);
}